.QuizList {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 100px;
    -ms-flex-positive: 1;
        flex-grow: 1;
    width: 100%;
    background: -webkit-gradient(linear, left top, right top, from(#fd8355), color-stop(36%, #f0576c), to(#f79cbd));
    background: -webkit-linear-gradient(left, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
    background: -o-linear-gradient(left, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
    background: linear-gradient(90deg, #fd8355 0%, #f0576c 36%, #f79cbd 100%);
}

.QuizList h1, h2 {
    color: #fff;
}

.QuizList ul {
    list-style: none;
    margin: 0;
    padding: 0;
}

.QuizList li {
    padding: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    -webkit-transition: background-color 0.3s;
    -o-transition: background-color 0.3s;
    transition: background-color 0.3s;
    border-radius: 5px;
}

.QuizList li:hover {
    background-color: rgba(90, 90, 90, 0.1);
}

.QuizList li a {
    display: inline-block;
    margin: -5px 5px -5px -5px;
    padding: 5px;
    width: 100%;
    text-decoration: none;
    color: #fff;
    -webkit-transition: color 0.3s;
    -o-transition: color 0.3s;
    transition: color 0.3s;
}

.QuizList li a:hover {
    /*color: #363f54;*/
}


.QuizList li div {
    display: -ms-flexbox;
    display: flex;
}


.QuizList li button {
    opacity: 0;
    -webkit-transition: opacity 0.3s ease-in-out;
    -o-transition: opacity 0.3s ease-in-out;
    transition: opacity 0.3s ease-in-out;
}

.QuizList li:hover button {
    opacity: 1;
}



